html,body{
margin:0;
padding:0;

}
#dark body{
background: rgb(2,0,36);
background: linear-gradient(90deg, rgba(104, 79, 130, 0.739) 0%, rgba(72, 46, 91, 0.488) 100%, rgba(36, 11, 69, 0.328) 100%);
background-image: linear-gradient(90deg, rgba(104, 79, 130, 0.739) 0%, rgba(72, 46, 91, 0.488) 100%, rgba(36, 11, 69, 0.328) 100%);
}
#light html{
 background: linear-gradient(90deg, rgb(234, 230, 229) 0%, rgb(81, 201, 212) 100%, rgb(125, 42, 10) 100%);
}

h1{text-align: center;
padding-top:30px;}
h2,p{
    padding-top:30px;
}
.i-anchor1{
    margin-bottom: 20px;
    
}
#dark a{
  color:rgba(187, 173, 242, 0.867);
}
.container{
  
    text-align: center;
    position: relative;
 
}
#dark .project-bg-wrapper{
 background: rgb(2,0,36);
background: linear-gradient(90deg, rgb(38, 36, 40) 0%, rgb(72, 46, 91) 100%, rgba(135, 111, 166, 0.366) 100%);
  color: white;
}
#light .project-bg-wrapper{
 background:  linear-gradient(90deg, rgb(234, 230, 229) 0%, rgb(81, 201, 212) 100%, rgb(125, 42, 10) 100%);
  color: rgb(49, 47, 47);
}
.project-wrapper{
    text-align: center;
    margin-top: 20vh;
}

.column {
  float: left;
  width: 25%;
  margin-left:200px;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

.i-proj1{
    width: 500px;
}
.projects{
  margin: 30px;
}

@media screen and (max-width: 600px) {
  .column {
    width: 100%;
    margin:0;
  }
.projects{
margin: 30px;

}
img.i-proj1{
  width: 400px;
}

}