  background-color: rgb(252, 241, 241);
  background: linear-gradient(90deg, rgb(234, 230, 229) 0%, rgb(133, 218, 226) 100%, rgb(125, 42, 10) 100%);
}
a{text-decoration: none;}
#dark .i{

 
 

 background: rgb(2,0,36);
background: linear-gradient(90deg, rgb(38, 36, 40) 0%, rgb(72, 46, 91) 100%, rgb(135, 111, 166) 100%);

  color:white
