.i {
  display: flex;
  height: 100vh;
 
}
.i-left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;

}
.i-right {
  flex: 1;
  position: relative;
 
}
.i-left-wrapper {
   /* padding: 50px;
  height: 60%; */
  margin-left:20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between; 
}

.i-intro {
  font-size: 30px;
  font-weight: 300;
}
.i-name {
  font-size: 60px;
  margin-bottom: 20px;
  
}
#name{
  margin-right:34vh;
}
.i-title {
  height: 60px;
  overflow: hidden;
  line-height: 1.5;
}
.i-title-wrapper {
  height: 100%;
  animation: move 10s ease-in-out infinite alternate;
}
.i-title-item {
  height: 90px;
  font-size: 30px;
  font-weight: bold;
  color: tomato;
  display: flex;
  align-items: center;
}
#light .i-title-item {
   color: tomato;
}
#dark .i-title-item{
  color: rgba(187, 173, 242, 0.867)}
.i-img{
width: 200px; 

border-radius: 100%;

}
p.i-desc{
  padding-bottom: 30px;
  line-height: 1.5;
}
.i-bg{
clip-path: polygon(100% 0%, 78% 53%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
  width: 100%;
  height:100%;
  position:absolute;
  top:0;
  right:0;

  

}
#light .i-bg{
 background: linear-gradient(90deg,  rgb(223, 144, 98) 0%, rgba(195, 165, 134, 0.077) 100%, rgba(190, 112, 81, 0.017) 100%);
}
#dark .i-bg{
background: rgb(2,0,36);
background: linear-gradient(90deg, rgba(104, 79, 130, 0.739) 0%, rgba(72, 46, 91, 0.488) 100%, rgba(36, 11, 69, 0.058) 100%);
}

/*buttons*/

button{
cursor: pointer;

/* CSS */

  align-items: center;
  background-clip: padding-box;
  border: 1px solid transparent;
  border-radius: .25rem;
  box-shadow: rgba(0, 0, 0, 0.02) 0 1px 3px 0;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inline-flex;
  font-family: system-ui,-apple-system,system-ui,"Helvetica Neue",Helvetica,Arial,sans-serif;
  font-size: 16px;
  font-weight: 600;
  justify-content: center;
  line-height: 1.25;
  margin: 0;
  min-height: 3rem;
  padding: calc(.875rem - 1px) calc(1.5rem - 1px);
  position: relative;
  text-decoration: none;
  transition: all 250ms;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: baseline;
  width: auto;
}
#light button{
 background-color: #d16b38;
}
#dark button{
 background-color: #9e542fdb;
}
.button-5:hover,
.button-5:focus {
  background-color:  #29c886;
  box-shadow: rgba(0, 0, 0, 0.1) 0 4px 12px;
}

.button-5:hover {
  transform: translateY(-1px);
}

.button-5:active {
  background-color:  #29c886;
  box-shadow: rgba(0, 0, 0, .06) 0 2px 4px;
  transform: translateY(0);
}




img{margin-left:8px}



